export const getGameResult = (totalScore: number): GameResult => {
  if (totalScore >= 40) {
    return {
      score: totalScore,
      title: "🌟 宇宙级默契情侣",
      description: "恭喜！你们的默契度已经超越了地球的引力范围，建议立即申请火星移民签证，因为地球已经容不下你们这对神仙眷侣了！",
      advice: "温馨提示：请准备好应对外星人的嫉妒，他们可能会派UFO来学习你们的相处秘诀。",
      compatibility: 99
    };
  } else if (totalScore >= 35) {
    return {
      score: totalScore,
      title: "💫 银河系知名情侣",
      description: "你们的感情已经在银河系小有名气，据说连北斗七星都在讨论你们的爱情故事。不过还需要努力才能达到宇宙级别哦！",
      advice: "建议每天对着星空许愿，说不定真的会有流星为你们改变轨道。",
      compatibility: 88
    };
  } else if (totalScore >= 30) {
    return {
      score: totalScore,
      title: "🎪 马戏团明星情侣",
      description: "你们的相处模式就像马戏团表演一样精彩，充满了惊喜和欢乐。观众们都被你们的默契震撼了！",
      advice: "记得偶尔给彼此一些'彩蛋'惊喜，保持这种有趣的互动模式。",
      compatibility: 75
    };
  } else if (totalScore >= 25) {
    return {
      score: totalScore,
      title: "🎮 游戏玩家情侣",
      description: "你们就像游戏中的队友，有时配合默契，有时也会'坑'对方一把。总体来说是个不错的组合！",
      advice: "建议多刷一些'情侣副本'，提升默契度和装备等级。",
      compatibility: 65
    };
  } else {
    return {
      score: totalScore,
      title: "🤖 AI学习中情侣",
      description: "你们的默契度还在AI学习阶段，需要更多的数据训练。不过不用担心，连最先进的AI都需要时间学习呢！",
      advice: "建议增加日常互动频率，多进行'深度学习'，相信很快就能升级到高级版本。",
      compatibility: 50
    };
  }
};