import { UserAnswers, CoupleGameResult, AnswerComparison, GameSession } from '../types/game';
import { questions } from '../data/questions';

// 生成邀请码
export const generateInviteCode = (): string => {
  return Math.random().toString(36).substring(2, 8).toUpperCase();
};

// 生成游戏会话ID
export const generateSessionId = (): string => {
  return Date.now().toString(36) + Math.random().toString(36).substring(2);
};

// 计算答案匹配度
export const compareAnswers = (
  initiatorAnswers: UserAnswers,
  participantAnswers: UserAnswers
): AnswerComparison[] => {
  return questions.map(question => {
    const initiatorAnswer = initiatorAnswers[question.id];
    const participantAnswer = participantAnswers[question.id];
    
    const initiatorOption = question.options.find(opt => opt.id === initiatorAnswer);
    const participantOption = question.options.find(opt => opt.id === participantAnswer);
    
    return {
      questionId: question.id,
      questionText: question.text,
      initiatorAnswer: initiatorOption?.text || '未回答',
      participantAnswer: participantOption?.text || '未回答',
      isMatching: initiatorAnswer === participantAnswer
    };
  });
};

// 计算双人游戏结果
export const calculateCoupleResult = (
  initiatorAnswers: UserAnswers,
  participantAnswers: UserAnswers
): CoupleGameResult => {
  const comparisons = compareAnswers(initiatorAnswers, participantAnswers);
  const matchingAnswers = comparisons.filter(comp => comp.isMatching).length;
  const totalQuestions = questions.length;
  const compatibilityPercentage = Math.round((matchingAnswers / totalQuestions) * 100);
  
  // 计算各自得分
  const initiatorScore = Object.entries(initiatorAnswers).reduce((sum, [questionId, answerId]) => {
    const question = questions.find(q => q.id === parseInt(questionId));
    const option = question?.options.find(opt => opt.id === answerId);
    return sum + (option?.value || 0);
  }, 0);
  
  const participantScore = Object.entries(participantAnswers).reduce((sum, [questionId, answerId]) => {
    const question = questions.find(q => q.id === parseInt(questionId));
    const option = question?.options.find(opt => opt.id === answerId);
    return sum + (option?.value || 0);
  }, 0);
  
  // 生成搞笑分析
  const { title, description, advice, funnyAnalysis } = generateFunnyAnalysis(compatibilityPercentage, matchingAnswers, totalQuestions);
  
  return {
    initiatorScore,
    participantScore,
    matchingAnswers,
    totalQuestions,
    compatibilityPercentage,
    title,
    description,
    advice,
    funnyAnalysis
  };
};

// 生成搞笑分析文案
const generateFunnyAnalysis = (
  percentage: number,
  matching: number,
  total: number
): { title: string; description: string; advice: string; funnyAnalysis: string } => {
  if (percentage >= 90) {
    return {
      title: '🎯 心有灵犀一点通',
      description: `你们的默契度高达${percentage}%！简直就是传说中的灵魂伴侣！`,
      advice: '继续保持这种神仙眷侣的状态，记得多交流哦～',
      funnyAnalysis: `哇塞！${matching}/${total}题都一样，你们是不是提前串通好了？还是说真的有心电感应？建议去买彩票，这运气绝了！ 💕`
    };
  } else if (percentage >= 70) {
    return {
      title: '💖 甜蜜情侣档',
      description: `${percentage}%的默契度说明你们很了解彼此，是一对很棒的情侣！`,
      advice: '偶尔的小分歧让感情更有趣，继续加油！',
      funnyAnalysis: `${matching}/${total}题匹配，不错不错！虽然不是100%同步，但这样才有惊喜嘛～毕竟完全一样的话，生活就太无聊了！ 😘`
    };
  } else if (percentage >= 50) {
    return {
      title: '🤔 磨合期情侣',
      description: `${percentage}%的匹配度表明你们还在互相了解的过程中。`,
      advice: '多沟通多交流，了解对方的想法很重要哦！',
      funnyAnalysis: `${matching}/${total}题一致，emmm...看来你们还需要多聊聊天！不过没关系，爱情就是在磨合中升温的嘛～建议多约会！ 🌟`
    };
  } else if (percentage >= 30) {
    return {
      title: '😅 互补型情侣',
      description: `${percentage}%的匹配度...你们真的是很有个性的一对！`,
      advice: '差异也是一种美，学会欣赏对方的不同吧！',
      funnyAnalysis: `只有${matching}/${total}题匹配...你们确定不是来自不同星球的吗？😂 不过话说回来，互补型情侣也很有意思啊，至少不会无聊！`
    };
  } else {
    return {
      title: '🤯 神秘组合',
      description: `${percentage}%的匹配度...这真是一个神奇的结果！`,
      advice: '也许你们需要更多时间来了解彼此，或者...重新考虑一下？',
      funnyAnalysis: `天哪！只有${matching}/${total}题一样！你们是不是故意选相反的答案来整蛊我？😱 要么就是缘分还没到，要么就是...算了，祝你们好运！`
    };
  }
};

// 本地存储游戏会话
export const saveGameSession = (session: GameSession, inviteCode?: string): void => {
  const sessions = getGameSessions();
  sessions[session.id] = session;
  localStorage.setItem('coupleSessions', JSON.stringify(sessions));
  
  // 如果提供了邀请码，建立邀请码到会话ID的映射
  if (inviteCode) {
    const codeMapping = getInviteCodeMapping();
    codeMapping[inviteCode] = session.id;
    localStorage.setItem('inviteCodeMapping', JSON.stringify(codeMapping));
  }
};

// 获取游戏会话
export const getGameSession = (sessionId: string): GameSession | null => {
  const sessions = getGameSessions();
  return sessions[sessionId] || null;
};

// 通过邀请码获取游戏会话
export const getGameSessionByInviteCode = (inviteCode: string): GameSession | null => {
  const codeMapping = getInviteCodeMapping();
  const sessionId = codeMapping[inviteCode];
  
  console.log('查找邀请码:', { inviteCode, sessionId, codeMapping });
  
  if (sessionId) {
    const session = getGameSession(sessionId);
    console.log('找到会话:', { sessionId, session });
    return session;
  }
  
  console.log('未找到对应的会话');
  return null;
};

// 获取所有游戏会话
export const getGameSessions = (): { [key: string]: GameSession } => {
  const stored = localStorage.getItem('coupleSessions');
  return stored ? JSON.parse(stored) : {};
};

// 获取邀请码映射
export const getInviteCodeMapping = (): { [key: string]: string } => {
  const stored = localStorage.getItem('inviteCodeMapping');
  return stored ? JSON.parse(stored) : {};
};

// 更新游戏会话
export const updateGameSession = (sessionId: string, updates: Partial<GameSession>): void => {
  const session = getGameSession(sessionId);
  if (session) {
    const updatedSession = { ...session, ...updates };
    saveGameSession(updatedSession);
  }
};

// 清理过期会话（24小时后过期）
export const cleanExpiredSessions = (): void => {
  const sessions = getGameSessions();
  const codeMapping = getInviteCodeMapping();
  const now = Date.now();
  const oneDayMs = 24 * 60 * 60 * 1000;
  
  // 收集要删除的会话ID
  const expiredSessionIds: string[] = [];
  
  Object.keys(sessions).forEach(sessionId => {
    if (now - sessions[sessionId].createdAt > oneDayMs) {
      expiredSessionIds.push(sessionId);
      delete sessions[sessionId];
    }
  });
  
  // 同时清理对应的邀请码映射
  Object.keys(codeMapping).forEach(inviteCode => {
    if (expiredSessionIds.includes(codeMapping[inviteCode])) {
      delete codeMapping[inviteCode];
    }
  });
  
  localStorage.setItem('coupleSessions', JSON.stringify(sessions));
  localStorage.setItem('inviteCodeMapping', JSON.stringify(codeMapping));
};