import React, { createContext, useContext, useState, ReactNode, useEffect } from 'react';
import { GameStep, UserAnswers, GameResult, GameMode, UserRole, CoupleGameResult, GameSession } from '../types/game';
import { getGameResult, questions } from '../data/questions';
import { 
  generateInviteCode, 
  generateSessionId, 
  calculateCoupleResult, 
  saveGameSession, 
  getGameSession, 
  getGameSessionByInviteCode,
  updateGameSession,
  cleanExpiredSessions
} from '../utils/coupleGame';

interface GameContextType {
  // 通用状态
  currentStep: GameStep;
  currentQuestionIndex: number;
  userAnswers: UserAnswers;
  gameResult: GameResult | null;
  
  // 双人游戏状态
  gameMode: GameMode;
  userRole: UserRole | null;
  inviteCode: string | null;
  sessionId: string | null;
  coupleResult: CoupleGameResult | null;
  partnerAnswers: UserAnswers;
  isPartnerReady: boolean;
  
  // 通用方法
  setCurrentStep: (step: GameStep) => void;
  setCurrentQuestionIndex: (index: number) => void;
  saveAnswer: (questionId: number, answer: string) => void;
  calculateResult: () => void;
  resetGame: () => void;
  
  // 双人游戏方法
  setGameMode: (mode: GameMode) => void;
  startCoupleGame: () => void;
  joinGameByCode: (code: string) => boolean;
  finishInitiatorQuiz: () => void;
  finishParticipantQuiz: () => void;
  checkPartnerStatus: () => void;
}

const GameContext = createContext<GameContextType | undefined>(undefined);

export const useGame = () => {
  const context = useContext(GameContext);
  if (!context) {
    throw new Error('useGame must be used within a GameProvider');
  }
  return context;
};

interface GameProviderProps {
  children: ReactNode;
}

export const GameProvider: React.FC<GameProviderProps> = ({ children }) => {
  // 通用状态
  const [currentStep, setCurrentStep] = useState<GameStep>('intro');
  const [currentQuestionIndex, setCurrentQuestionIndex] = useState(0);
  const [userAnswers, setUserAnswers] = useState<UserAnswers>({});
  const [gameResult, setGameResult] = useState<GameResult | null>(null);
  
  // 双人游戏状态
  const [gameMode, setGameMode] = useState<GameMode>('single');
  const [userRole, setUserRole] = useState<UserRole | null>(null);
  const [inviteCode, setInviteCode] = useState<string | null>(null);
  const [sessionId, setSessionId] = useState<string | null>(null);
  const [coupleResult, setCoupleResult] = useState<CoupleGameResult | null>(null);
  const [partnerAnswers, setPartnerAnswers] = useState<UserAnswers>({});
  const [isPartnerReady, setIsPartnerReady] = useState(false);

  // 清理过期会话
  useEffect(() => {
    cleanExpiredSessions();
  }, []);

  // 检查URL参数，自动加入游戏
  useEffect(() => {
    const urlParams = new URLSearchParams(window.location.search);
    const code = urlParams.get('invite');
    if (code) {
      joinGameByCode(code);
    }
  }, []);

  const saveAnswer = (questionId: number, answer: string) => {
    setUserAnswers(prev => ({
      ...prev,
      [questionId]: answer
    }));
    
    // 如果是双人模式，实时保存到会话
    if (gameMode === 'couple' && sessionId) {
      const session = getGameSession(sessionId);
      if (session) {
        if (userRole === 'initiator') {
          updateGameSession(sessionId, {
            initiatorAnswers: { ...userAnswers, [questionId]: answer }
          });
        } else if (userRole === 'participant') {
          updateGameSession(sessionId, {
            participantAnswers: { ...userAnswers, [questionId]: answer }
          });
        }
      }
    }
  };

  const calculateResult = () => {
    if (gameMode === 'single') {
      const totalScore = Object.entries(userAnswers).reduce((sum, [questionId, answerId]) => {
        const question = questions.find((q) => q.id === parseInt(questionId));
        if (question) {
          const option = question.options.find((opt) => opt.id === answerId);
          if (option) {
            return sum + option.value;
          }
        }
        return sum;
      }, 0);
      
      const result = getGameResult(totalScore);
      setGameResult(result);
      setCurrentStep('result');
    }
  };

  const startCoupleGame = () => {
    const newSessionId = generateSessionId();
    const newInviteCode = generateInviteCode();
    
    const session: GameSession = {
      id: newSessionId,
      initiatorAnswers: {},
      participantAnswers: {},
      status: 'waiting',
      createdAt: Date.now()
    };
    
    // 保存会话并建立邀请码映射
    saveGameSession(session, newInviteCode);
    
    setGameMode('couple');
    setUserRole('initiator');
    setSessionId(newSessionId);
    setInviteCode(newInviteCode);
    setCurrentStep('quiz');
    
    // 更新URL以包含邀请码
    const newUrl = `${window.location.origin}${window.location.pathname}?invite=${newInviteCode}`;
    window.history.replaceState({}, '', newUrl);
  };

  const joinGameByCode = (code: string): boolean => {
    // 通过邀请码查找会话
    const session = getGameSessionByInviteCode(code);
    
    if (!session || session.status === 'completed') {
      console.log('邀请码无效或游戏已结束:', { code, session });
      return false;
    }
    
    setGameMode('couple');
    setUserRole('participant');
    setSessionId(session.id);
    setInviteCode(code);
    setCurrentStep('quiz');
    
    updateGameSession(session.id, { status: 'in_progress' });
    return true;
  };

  const finishInitiatorQuiz = () => {
    if (sessionId) {
      updateGameSession(sessionId, {
        initiatorAnswers: userAnswers,
        status: 'waiting'
      });
      setCurrentStep('waiting');
    }
  };

  const finishParticipantQuiz = () => {
    if (sessionId) {
      const session = getGameSession(sessionId);
      if (session) {
        updateGameSession(sessionId, {
          participantAnswers: userAnswers,
          status: 'completed'
        });
        
        // 计算双人结果
        const result = calculateCoupleResult(session.initiatorAnswers, userAnswers);
        setCoupleResult(result);
        setPartnerAnswers(session.initiatorAnswers);
        setCurrentStep('result');
      }
    }
  };

  const checkPartnerStatus = () => {
    if (sessionId) {
      const session = getGameSession(sessionId);
      if (session && session.status === 'completed') {
        const result = calculateCoupleResult(userAnswers, session.participantAnswers);
        setCoupleResult(result);
        setPartnerAnswers(session.participantAnswers);
        setIsPartnerReady(true);
        setCurrentStep('result');
      }
    }
  };

  const resetGame = () => {
    setCurrentStep('intro');
    setCurrentQuestionIndex(0);
    setUserAnswers({});
    setGameResult(null);
    setGameMode('single');
    setUserRole(null);
    setInviteCode(null);
    setSessionId(null);
    setCoupleResult(null);
    setPartnerAnswers({});
    setIsPartnerReady(false);
    
    // 清理URL参数
    window.history.replaceState({}, '', window.location.pathname);
  };

  const value = {
    // 通用状态
    currentStep,
    currentQuestionIndex,
    userAnswers,
    gameResult,
    
    // 双人游戏状态
    gameMode,
    userRole,
    inviteCode,
    sessionId,
    coupleResult,
    partnerAnswers,
    isPartnerReady,
    
    // 通用方法
    setCurrentStep,
    setCurrentQuestionIndex,
    saveAnswer,
    calculateResult,
    resetGame,
    
    // 双人游戏方法
    setGameMode,
    startCoupleGame,
    joinGameByCode,
    finishInitiatorQuiz,
    finishParticipantQuiz,
    checkPartnerStatus
  };

  return (
    <GameContext.Provider value={value}>
      {children}
    </GameContext.Provider>
  );
};