import React, { useState } from 'react';
import { useGame } from '../contexts/GameContext';
import { Share2, RefreshCw, Copy, Download } from 'lucide-react';

const ResultPage: React.FC = () => {
  const { gameResult, resetGame } = useGame();
  const [copied, setCopied] = useState(false);

  if (!gameResult) {
    return <div>加载中...</div>;
  }

  const handleShare = async () => {
    const shareText = `我刚完成了AI情侣默契度分析！\n\n结果：${gameResult.title}\n默契度：${gameResult.compatibility}%\n\n${gameResult.description}\n\n快来测试你们的默契度吧！`;
    
    if (navigator.share) {
      try {
        await navigator.share({
          title: 'AI情侣默契度分析结果',
          text: shareText,
          url: window.location.href
        });
      } catch (err) {
        console.log('分享失败:', err);
      }
    } else {
      // 复制到剪贴板
      try {
        await navigator.clipboard.writeText(shareText + '\n\n' + window.location.href);
        setCopied(true);
        setTimeout(() => setCopied(false), 2000);
      } catch (err) {
        console.log('复制失败:', err);
      }
    }
  };

  const handleDownload = () => {
    // 创建一个简单的文本报告
    const reportContent = `
===========================================
        AI情侣默契度分析报告
===========================================

测试结果：${gameResult.title}
总分：${gameResult.score}分
默契度：${gameResult.compatibility}%

详细分析：
${gameResult.description}

专业建议：
${gameResult.advice}

===========================================
报告生成时间：${new Date().toLocaleString()}
© 2024 AI情侣分析实验室
===========================================
    `;

    const blob = new Blob([reportContent], { type: 'text/plain;charset=utf-8' });
    const url = URL.createObjectURL(blob);
    const a = document.createElement('a');
    a.href = url;
    a.download = `情侣默契度分析报告_${new Date().toISOString().split('T')[0]}.txt`;
    document.body.appendChild(a);
    a.click();
    document.body.removeChild(a);
    URL.revokeObjectURL(url);
  };

  const getScoreColor = (score: number) => {
    if (score >= 90) return 'text-green-600';
    if (score >= 70) return 'text-blue-600';
    if (score >= 50) return 'text-yellow-600';
    return 'text-red-600';
  };

  const getScoreBg = (score: number) => {
    if (score >= 90) return 'from-green-400 to-green-600';
    if (score >= 70) return 'from-blue-400 to-blue-600';
    if (score >= 50) return 'from-yellow-400 to-yellow-600';
    return 'from-red-400 to-red-600';
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-blue-50 to-pink-50 flex items-center justify-center p-4">
      <div className="max-w-4xl w-full">
        {/* 结果标题 */}
        <div className="text-center mb-8">
          <div className="inline-flex items-center justify-center w-20 h-20 bg-gradient-to-r from-blue-100 to-pink-100 rounded-full mb-4">
            <span className="text-3xl">🎉</span>
          </div>
          <h1 className="text-3xl md:text-4xl font-bold text-gray-800 mb-2">
            分析完成！
          </h1>
          <p className="text-gray-600">
            基于AI深度学习算法的专业分析结果
          </p>
        </div>

        {/* 主要结果卡片 */}
        <div className="bg-white rounded-2xl shadow-xl p-8 mb-6">
          <div className="text-center mb-8">
            <h2 className="text-2xl md:text-3xl font-bold text-gray-800 mb-4">
              {gameResult.title}
            </h2>
            
            {/* 分数显示 */}
            <div className="flex justify-center items-center mb-6">
              <div className={`text-6xl md:text-7xl font-bold ${getScoreColor(gameResult.compatibility)} mr-4`}>
                {gameResult.compatibility}
              </div>
              <div className="text-left">
                <div className="text-2xl font-bold text-gray-600">%</div>
                <div className="text-sm text-gray-500">默契度</div>
              </div>
            </div>

            {/* 进度条 */}
            <div className="w-full bg-gray-200 rounded-full h-4 mb-6">
              <div
                className={`bg-gradient-to-r ${getScoreBg(gameResult.compatibility)} h-4 rounded-full transition-all duration-1000 ease-out`}
                style={{ width: `${gameResult.compatibility}%` }}
              ></div>
            </div>
          </div>

          {/* 详细分析 */}
          <div className="space-y-6">
            <div className="bg-gradient-to-r from-blue-50 to-pink-50 rounded-xl p-6">
              <h3 className="text-xl font-bold text-gray-800 mb-3 flex items-center">
                🔍 详细分析报告
              </h3>
              <p className="text-gray-700 leading-relaxed">
                {gameResult.description}
              </p>
            </div>

            <div className="bg-gradient-to-r from-purple-50 to-indigo-50 rounded-xl p-6">
              <h3 className="text-xl font-bold text-gray-800 mb-3 flex items-center">
                💡 专业建议
              </h3>
              <p className="text-gray-700 leading-relaxed">
                {gameResult.advice}
              </p>
            </div>

            {/* 统计信息 */}
            <div className="grid md:grid-cols-3 gap-4">
              <div className="bg-blue-50 rounded-xl p-4 text-center">
                <div className="text-2xl font-bold text-blue-600">{gameResult.score}</div>
                <div className="text-sm text-gray-600">总分</div>
              </div>
              <div className="bg-pink-50 rounded-xl p-4 text-center">
                <div className="text-2xl font-bold text-pink-600">{gameResult.compatibility}%</div>
                <div className="text-sm text-gray-600">匹配度</div>
              </div>
              <div className="bg-purple-50 rounded-xl p-4 text-center">
                <div className="text-2xl font-bold text-purple-600">5/5</div>
                <div className="text-sm text-gray-600">完成题目</div>
              </div>
            </div>
          </div>
        </div>

        {/* 操作按钮 */}
        <div className="flex flex-col md:flex-row gap-4 mb-6">
          <button
            onClick={handleShare}
            className="flex-1 bg-gradient-to-r from-blue-600 to-pink-600 hover:from-blue-700 hover:to-pink-700 text-white font-bold py-4 px-6 rounded-xl transition-all duration-300 transform hover:scale-105 shadow-lg flex items-center justify-center"
          >
            {copied ? (
              <>
                <Copy className="w-5 h-5 mr-2" />
                已复制到剪贴板！
              </>
            ) : (
              <>
                <Share2 className="w-5 h-5 mr-2" />
                分享结果给TA
              </>
            )}
          </button>
          
          <button
            onClick={handleDownload}
            className="flex-1 bg-gradient-to-r from-purple-600 to-indigo-600 hover:from-purple-700 hover:to-indigo-700 text-white font-bold py-4 px-6 rounded-xl transition-all duration-300 transform hover:scale-105 shadow-lg flex items-center justify-center"
          >
            <Download className="w-5 h-5 mr-2" />
            下载报告
          </button>
          
          <button
            onClick={resetGame}
            className="flex-1 bg-gray-600 hover:bg-gray-700 text-white font-bold py-4 px-6 rounded-xl transition-all duration-300 transform hover:scale-105 shadow-lg flex items-center justify-center"
          >
            <RefreshCw className="w-5 h-5 mr-2" />
            重新测试
          </button>
        </div>

        {/* 底部说明 */}
        <div className="bg-white rounded-xl p-6 text-center">
          <p className="text-gray-600 mb-2">
            🎭 这是一个娱乐性测试，结果仅供参考和娱乐
          </p>
          <p className="text-sm text-gray-500">
            真正的感情需要在日常生活中慢慢培养和经营 ❤️
          </p>
        </div>
      </div>
    </div>
  );
};

export default ResultPage;