import React, { useState } from 'react';
import { useGame } from '../contexts/GameContext';
import { Heart, Download, Share2, RotateCcw, CheckCircle, XCircle, Trophy, Zap } from 'lucide-react';
import { toast } from 'sonner';
import { compareAnswers } from '../utils/coupleGame';
import { questions } from '../data/questions';

const CoupleResultPage: React.FC = () => {
  const { coupleResult, userAnswers, partnerAnswers, userRole, resetGame } = useGame();
  const [activeTab, setActiveTab] = useState<'summary' | 'details'>('summary');
  
  if (!coupleResult) {
    return (
      <div className="min-h-screen bg-gradient-to-br from-pink-50 to-purple-50 flex items-center justify-center">
        <div className="text-center">
          <p className="text-gray-600">加载结果中...</p>
        </div>
      </div>
    );
  }
  
  const comparisons = compareAnswers(
    userRole === 'initiator' ? userAnswers : partnerAnswers,
    userRole === 'initiator' ? partnerAnswers : userAnswers
  );
  
  const handleShare = async () => {
    const shareText = `我们的情侣默契度测试结果出炉啦！\n\n💕 匹配度：${coupleResult.compatibilityPercentage}%\n🎯 ${coupleResult.title}\n\n${coupleResult.description}\n\n快来测测你们的默契度吧！`;
    
    if (navigator.share) {
      try {
        await navigator.share({
          title: 'AI双人情侣默契度测试结果',
          text: shareText,
          url: window.location.origin
        });
      } catch (err) {
        console.log('分享取消');
      }
    } else {
      try {
        await navigator.clipboard.writeText(shareText);
        toast.success('结果已复制到剪贴板！');
      } catch (err) {
        toast.error('分享失败');
      }
    }
  };
  
  const handleDownload = () => {
    const reportContent = `
=== AI双人情侣默契度测试报告 ===

📊 测试结果：
• 匹配度：${coupleResult.compatibilityPercentage}%
• 匹配题目：${coupleResult.matchingAnswers}/${coupleResult.totalQuestions}
• 评级：${coupleResult.title}

📝 详细分析：
${coupleResult.description}

💡 建议：
${coupleResult.advice}

😄 趣味分析：
${coupleResult.funnyAnalysis}

=== 详细对比 ===
${comparisons.map((comp, index) => 
  `\n${index + 1}. ${comp.questionText}\n   你的答案：${comp.initiatorAnswer}\n   TA的答案：${comp.participantAnswer}\n   ${comp.isMatching ? '✅ 一致' : '❌ 不一致'}`
).join('')}

---
生成时间：${new Date().toLocaleString()}
来源：AI双人情侣默契度测试
    `;
    
    const blob = new Blob([reportContent], { type: 'text/plain;charset=utf-8' });
    const url = URL.createObjectURL(blob);
    const a = document.createElement('a');
    a.href = url;
    a.download = `情侣默契度测试报告_${new Date().toISOString().split('T')[0]}.txt`;
    document.body.appendChild(a);
    a.click();
    document.body.removeChild(a);
    URL.revokeObjectURL(url);
    
    toast.success('报告已下载！');
  };
  
  const getCompatibilityColor = (percentage: number) => {
    if (percentage >= 80) return 'text-green-600';
    if (percentage >= 60) return 'text-blue-600';
    if (percentage >= 40) return 'text-yellow-600';
    return 'text-red-600';
  };
  
  const getCompatibilityBgColor = (percentage: number) => {
    if (percentage >= 80) return 'from-green-500 to-emerald-500';
    if (percentage >= 60) return 'from-blue-500 to-cyan-500';
    if (percentage >= 40) return 'from-yellow-500 to-orange-500';
    return 'from-red-500 to-pink-500';
  };
  
  return (
    <div className="min-h-screen bg-gradient-to-br from-pink-50 to-purple-50 p-4">
      <div className="max-w-4xl mx-auto">
        {/* 标题区域 */}
        <div className="text-center mb-8">
          <div className="flex justify-center items-center mb-4">
            <Trophy className="w-12 h-12 text-yellow-500" />
          </div>
          <h1 className="text-3xl md:text-4xl font-bold text-gray-800 mb-4">
            🎉 默契度测试结果
          </h1>
          <p className="text-lg text-gray-600">
            你们的爱情默契度分析报告新鲜出炉！
          </p>
        </div>
        
        {/* 标签切换 */}
        <div className="flex justify-center mb-8">
          <div className="bg-white rounded-xl p-1 shadow-lg">
            <button
              onClick={() => setActiveTab('summary')}
              className={`px-6 py-3 rounded-lg font-semibold transition-all duration-300 ${
                activeTab === 'summary'
                  ? 'bg-gradient-to-r from-pink-600 to-purple-600 text-white shadow-md'
                  : 'text-gray-600 hover:text-gray-800'
              }`}
            >
              📊 总体分析
            </button>
            <button
              onClick={() => setActiveTab('details')}
              className={`px-6 py-3 rounded-lg font-semibold transition-all duration-300 ${
                activeTab === 'details'
                  ? 'bg-gradient-to-r from-pink-600 to-purple-600 text-white shadow-md'
                  : 'text-gray-600 hover:text-gray-800'
              }`}
            >
              🔍 详细对比
            </button>
          </div>
        </div>
        
        {activeTab === 'summary' ? (
          /* 总体分析页面 */
          <div className="space-y-6">
            {/* 匹配度展示 */}
            <div className="bg-white rounded-2xl shadow-xl p-8">
              <div className="text-center mb-8">
                <div className="relative inline-block">
                  <div className={`w-32 h-32 rounded-full bg-gradient-to-r ${getCompatibilityBgColor(coupleResult.compatibilityPercentage)} flex items-center justify-center mx-auto mb-4 shadow-lg`}>
                    <span className="text-4xl font-bold text-white">
                      {coupleResult.compatibilityPercentage}%
                    </span>
                  </div>
                  <div className="absolute -top-2 -right-2">
                    <Heart className="w-8 h-8 text-red-500 animate-pulse" />
                  </div>
                </div>
                <h2 className={`text-3xl font-bold mb-2 ${getCompatibilityColor(coupleResult.compatibilityPercentage)}`}>
                  {coupleResult.title}
                </h2>
                <p className="text-gray-600 text-lg">
                  你们在 {coupleResult.totalQuestions} 道题中有 {coupleResult.matchingAnswers} 题答案一致
                </p>
              </div>
              
              {/* 分析描述 */}
              <div className="bg-gradient-to-r from-blue-50 to-pink-50 rounded-xl p-6 mb-6">
                <h3 className="font-semibold text-gray-800 mb-3 flex items-center">
                  <Zap className="w-5 h-5 mr-2 text-yellow-500" />
                  专业分析
                </h3>
                <p className="text-gray-700 leading-relaxed">
                  {coupleResult.description}
                </p>
              </div>
              
              {/* 搞笑分析 */}
              <div className="bg-gradient-to-r from-yellow-50 to-orange-50 rounded-xl p-6 mb-6">
                <h3 className="font-semibold text-gray-800 mb-3">😂 趣味解读</h3>
                <p className="text-gray-700 leading-relaxed">
                  {coupleResult.funnyAnalysis}
                </p>
              </div>
              
              {/* 建议 */}
              <div className="bg-gradient-to-r from-green-50 to-teal-50 rounded-xl p-6">
                <h3 className="font-semibold text-gray-800 mb-3">💡 爱情小贴士</h3>
                <p className="text-gray-700 leading-relaxed">
                  {coupleResult.advice}
                </p>
              </div>
            </div>
          </div>
        ) : (
          /* 详细对比页面 */
          <div className="bg-white rounded-2xl shadow-xl p-8">
            <h2 className="text-2xl font-bold text-gray-800 mb-6 text-center">
              📋 逐题对比分析
            </h2>
            
            <div className="space-y-4">
              {comparisons.map((comparison, index) => (
                <div key={comparison.questionId} className="border border-gray-200 rounded-xl p-6">
                  <div className="flex items-start justify-between mb-4">
                    <h3 className="font-semibold text-gray-800 flex-1">
                      {index + 1}. {comparison.questionText}
                    </h3>
                    <div className="ml-4">
                      {comparison.isMatching ? (
                        <CheckCircle className="w-6 h-6 text-green-500" />
                      ) : (
                        <XCircle className="w-6 h-6 text-red-500" />
                      )}
                    </div>
                  </div>
                  
                  <div className="grid md:grid-cols-2 gap-4">
                    <div className={`p-4 rounded-lg ${
                      comparison.isMatching ? 'bg-green-50 border border-green-200' : 'bg-gray-50 border border-gray-200'
                    }`}>
                      <p className="text-sm text-gray-600 mb-1">
                        {userRole === 'initiator' ? '你的答案' : 'TA的答案'}：
                      </p>
                      <p className="font-medium text-gray-800">
                        {comparison.initiatorAnswer}
                      </p>
                    </div>
                    
                    <div className={`p-4 rounded-lg ${
                      comparison.isMatching ? 'bg-green-50 border border-green-200' : 'bg-gray-50 border border-gray-200'
                    }`}>
                      <p className="text-sm text-gray-600 mb-1">
                        {userRole === 'initiator' ? 'TA的答案' : '你的答案'}：
                      </p>
                      <p className="font-medium text-gray-800">
                        {comparison.participantAnswer}
                      </p>
                    </div>
                  </div>
                  
                  <div className="mt-3 text-center">
                    <span className={`inline-flex items-center px-3 py-1 rounded-full text-sm font-medium ${
                      comparison.isMatching 
                        ? 'bg-green-100 text-green-800' 
                        : 'bg-red-100 text-red-800'
                    }`}>
                      {comparison.isMatching ? '✅ 默契一致' : '❌ 想法不同'}
                    </span>
                  </div>
                </div>
              ))}
            </div>
          </div>
        )}
        
        {/* 操作按钮 */}
        <div className="flex flex-wrap justify-center gap-4 mt-8">
          <button
            onClick={handleShare}
            className="bg-gradient-to-r from-pink-600 to-purple-600 hover:from-pink-700 hover:to-purple-700 text-white font-bold py-3 px-6 rounded-xl transition-all duration-300 flex items-center space-x-2 shadow-lg"
          >
            <Share2 className="w-5 h-5" />
            <span>分享结果</span>
          </button>
          
          <button
            onClick={handleDownload}
            className="bg-gradient-to-r from-blue-600 to-teal-600 hover:from-blue-700 hover:to-teal-700 text-white font-bold py-3 px-6 rounded-xl transition-all duration-300 flex items-center space-x-2 shadow-lg"
          >
            <Download className="w-5 h-5" />
            <span>下载报告</span>
          </button>
          
          <button
            onClick={resetGame}
            className="bg-gray-600 hover:bg-gray-700 text-white font-semibold py-3 px-6 rounded-xl transition-colors flex items-center space-x-2"
          >
            <RotateCcw className="w-5 h-5" />
            <span>重新测试</span>
          </button>
        </div>
        
        {/* 底部信息 */}
        <div className="text-center mt-8 text-gray-500 text-sm">
          <p>🎉 恭喜完成双人默契度测试！记得经常和另一半交流哦～</p>
        </div>
      </div>
    </div>
  );
};

export default CoupleResultPage;