import React from 'react';
import { useGame } from '../contexts/GameContext';
import { questions } from '../data/questions';
import { ChevronLeft, ChevronRight, Users, User } from 'lucide-react';

const QuizPage: React.FC = () => {
  const {
    currentQuestionIndex,
    setCurrentQuestionIndex,
    saveAnswer,
    calculateResult,
    userAnswers,
    gameMode,
    userRole,
    finishInitiatorQuiz,
    finishParticipantQuiz
  } = useGame();

  const currentQuestion = questions[currentQuestionIndex];
  const progress = ((currentQuestionIndex + 1) / questions.length) * 100;
  const isLastQuestion = currentQuestionIndex === questions.length - 1;
  const hasAnswered = userAnswers[currentQuestion.id] !== undefined;

  const handleAnswerSelect = (optionId: string) => {
    saveAnswer(currentQuestion.id, optionId);
  };

  const handleNext = () => {
    if (isLastQuestion) {
      if (gameMode === 'couple') {
        if (userRole === 'initiator') {
          finishInitiatorQuiz();
        } else {
          finishParticipantQuiz();
        }
      } else {
        calculateResult();
      }
    } else {
      setCurrentQuestionIndex(currentQuestionIndex + 1);
    }
  };

  const handlePrevious = () => {
    if (currentQuestionIndex > 0) {
      setCurrentQuestionIndex(currentQuestionIndex - 1);
    }
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-blue-50 to-pink-50 flex items-center justify-center p-4">
      <div className="max-w-3xl w-full">
        {/* 进度条 */}
        <div className="mb-8">
          <div className="flex justify-between items-center mb-2">
            <span className="text-sm font-medium text-gray-600">
              问题 {currentQuestionIndex + 1} / {questions.length}
            </span>
            <span className="text-sm font-medium text-gray-600">
              {Math.round(progress)}% 完成
            </span>
          </div>
          <div className="w-full bg-gray-200 rounded-full h-3">
            <div
              className="bg-gradient-to-r from-blue-500 to-pink-500 h-3 rounded-full transition-all duration-500 ease-out"
              style={{ width: `${progress}%` }}
            ></div>
          </div>
        </div>

        {/* 问题卡片 */}
        <div className="bg-white rounded-2xl shadow-xl p-8 mb-6">
          <div className="text-center mb-8">
            <div className="inline-flex items-center justify-center w-16 h-16 bg-gradient-to-r from-blue-100 to-pink-100 rounded-full mb-4">
              <span className="text-2xl font-bold text-gray-700">
                {currentQuestionIndex + 1}
              </span>
            </div>
            {gameMode === 'couple' && (
              <div className="flex items-center justify-center mb-4">
                {userRole === 'initiator' ? (
                  <div className="flex items-center text-blue-600">
                    <Users className="w-5 h-5 mr-2" />
                    <span className="font-medium">发起者答题</span>
                  </div>
                ) : (
                  <div className="flex items-center text-pink-600">
                    <User className="w-5 h-5 mr-2" />
                    <span className="font-medium">参与者答题</span>
                  </div>
                )}
              </div>
            )}
            <h2 className="text-2xl md:text-3xl font-bold text-gray-800 mb-4">
              {currentQuestion.text}
            </h2>
            <p className="text-gray-600">
              {gameMode === 'couple' 
                ? (userRole === 'initiator' ? '请根据你的真实想法选择' : '请根据你的真实想法选择，完成后将与对方答案进行对比')
                : '请选择最符合你想法的选项'
              }
            </p>
          </div>

          {/* 选项 */}
          <div className="space-y-4 mb-8">
            {currentQuestion.options.map((option) => {
              const isSelected = userAnswers[currentQuestion.id] === option.id;
              return (
                <button
                  key={option.id}
                  onClick={() => handleAnswerSelect(option.id)}
                  className={`w-full p-6 text-left rounded-xl border-2 transition-all duration-300 transform hover:scale-[1.02] ${
                    isSelected
                      ? 'border-blue-500 bg-blue-50 shadow-lg'
                      : 'border-gray-200 bg-white hover:border-blue-300 hover:shadow-md'
                  }`}
                >
                  <div className="flex items-center">
                    <div className={`w-6 h-6 rounded-full border-2 mr-4 flex items-center justify-center ${
                      isSelected
                        ? 'border-blue-500 bg-blue-500'
                        : 'border-gray-300'
                    }`}>
                      {isSelected && (
                        <div className="w-3 h-3 bg-white rounded-full"></div>
                      )}
                    </div>
                    <div className="flex-1">
                      <span className="text-lg font-medium text-gray-800">
                        {option.text}
                      </span>
                    </div>
                  </div>
                </button>
              );
            })}
          </div>

          {/* 导航按钮 */}
          <div className="flex justify-between items-center">
            <button
              onClick={handlePrevious}
              disabled={currentQuestionIndex === 0}
              className={`flex items-center px-6 py-3 rounded-xl font-medium transition-all duration-300 ${
                currentQuestionIndex === 0
                  ? 'text-gray-400 cursor-not-allowed'
                  : 'text-gray-600 hover:text-gray-800 hover:bg-gray-100'
              }`}
            >
              <ChevronLeft className="w-5 h-5 mr-2" />
              上一题
            </button>

            <button
              onClick={handleNext}
              disabled={!hasAnswered}
              className={`flex items-center px-8 py-3 rounded-xl font-bold transition-all duration-300 transform hover:scale-105 ${
                hasAnswered
                  ? 'bg-gradient-to-r from-blue-600 to-pink-600 hover:from-blue-700 hover:to-pink-700 text-white shadow-lg'
                  : 'bg-gray-300 text-gray-500 cursor-not-allowed'
              }`}
            >
              {isLastQuestion ? '查看结果' : '下一题'}
              {!isLastQuestion && <ChevronRight className="w-5 h-5 ml-2" />}
              {isLastQuestion && <span className="ml-2">🎉</span>}
            </button>
          </div>
        </div>

        {/* 底部提示 */}
        <div className="text-center text-gray-500 text-sm">
          <p>💡 请根据真实想法选择，这样分析结果会更准确哦</p>
        </div>
      </div>
    </div>
  );
};

export default QuizPage;