import React from 'react';
import { GameProvider, useGame } from './contexts/GameContext';
import IntroPage from './components/IntroPage';
import QuizPage from './components/QuizPage';
import ResultPage from './components/ResultPage';
import WaitingPage from './components/WaitingPage';
import CoupleResultPage from './components/CoupleResultPage';

const GameContent: React.FC = () => {
  const { currentStep } = useGame();

  switch (currentStep) {
    case 'intro':
      return <IntroPage />;
    case 'quiz':
      return <QuizPage />;
    case 'waiting':
      return <WaitingPage />;
    case 'result':
      return <ResultPage />;
    case 'coupleResult':
      return <CoupleResultPage />;
    default:
      return <IntroPage />;
  }
};

function App() {
  return (
    <GameProvider>
      <div className="App">
        <GameContent />
      </div>
    </GameProvider>
  );
}

export default App;
